.image {
    position: relative;
    overflow: hidden;
    & img {
        display: block !important;
        width: 100%;
        height: 100%;
        object-position: center;
        position: relative;
        will-change: filter;
        transition: filter 0.3s ease;
        filter: blur(8px);
    }
    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        will-change: background-position-x, background-position-y, opacity;
        background-color: rgba(155,155,155,0.15);
        background-image: linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0.6) 50%, rgba(255,255,255,0) 100%);
        background-size: 30% 100%, contain;
        background-repeat: no-repeat;
        background-position-x: 100%, center;
        background-position-y: 100%, center;
        animation: shimmer 1.5s infinite linear;
        transition: opacity 0.6s ease;
        z-index: 2;
        opacity: 1;
    }
    &:has(.lazyloading:not(.lazyloaded)) {
        img.lazyload, img.lazyloading {
            opacity: 1;
            z-index: 1;
        }   
    }
    &:has(.lazyloaded) {
        img.lazyloaded {
            filter: blur(0);
        }
        &::after {
            opacity: 0;
        }
    }
    &.cover img {
        object-fit: cover;
    }
    &.contain img {
        object-fit: contain;
    }
}
@keyframes shimmer {
    from {
        background-position-x: 150%, center;
    }
    to {
        background-position-x: -150%, center;
    }
}

.--font{
    &-light{
        font-weight: 300;
    }
    &-regular{
        font-weight: 400;
    }
    &-medium{
        font-weight: 500;
    }
    &-semibold{
        font-weight: 600;
    }
    &-bold{
        font-weight: 700;
    }
}

.--inline{
    display: inline;
}
.--inline-block{
    display: inline-block;
}
.--block{
    display: block;
}
.--hidden{
    display: none;
}

.--no-padding{
    padding: 0;
    &-x{
        padding-left: 0;
        padding-right: 0;
    }
    &-y{
        padding-top: 0;
        padding-right: 0;
    }
}

.--no-margin{
    margin: 0;
    &-x{
        margin-left: 0;
        margin-right: 0;
    }
    &-y{
        margin-top: 0;
        margin-right: 0;
    }
}

.--mx-auto{
    margin-left: auto;
    margin-right: auto;
}

.--no-overflow{
    overflow: hidden;
    &-x{
        overflow-x: hidden;
    }
    &-y{
        overflow-y: hidden;
    }
}